@use "variables" as v;

/** Media Queries **/
@mixin telefono {
	@media (min-width: v.$telefono) {
		@content;
	}
}

@mixin tablet {
	@media (min-width: v.$tablet) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: v.$desktop) {
		@content;
	}
}

@mixin boton($color, $texto) {
	background-color: $color;
	font-size: 2rem;
	padding: 1.2rem 3rem;
	border-radius: 1rem;
	font-weight: 700;
	display: block;
	width: 100%;
	text-align: center;
	color: $texto;
	text-decoration: none;
	transition: background-color 0.3s ease;
	border: none;

	&:hover {
		cursor: pointer;
		background-color: darken($color, 10);
	}

	@include tablet {
		width: auto;
	}
}

@mixin grid($columnas, $espaciado) {
	display: grid;
	gap: $espaciado;
	grid-template-columns: repeat($columnas, 1fr);
}
